
    import { Vue } from 'vue-class-component';
    import AuthenticationService from '../services/authenticationService';
    import { ToasterService } from '../services/toasterService';
    import TarsheedService from "../services/tarsheedServices";

    export default class CalculatorComponent extends Vue {
        meterNumber = "";
        isLoader = false;
        selectedBuildingType = { id: 0, name:'' } as any;
        selectedHousingType = { id: 1, name: 'عوائل' };
        buildingTypes = [{ id: 1, name: 'مباني سكنية' }, { id: 2, name: 'مباني إدارية' },
        { id: 3, name: 'مباني تعليمية' }, { id: 4, name: 'مساجد' }, { id: 5, name: 'مستشفي' },
        { id: 6, name: 'دار إيواء مسنين واحتياجات خاصة' }, { id: 7, name: 'مركز ايواء إصلاحي' }

        ];
        housingTypes = [{ id: 1, name: 'عوائل' }, { id: 2, name: 'سكن عمال' }, { id: 3, name: 'سكن طلاب ' }, { id: 4, name: 'سكن طالبات' }, { id: 5, name: 'سكن موظفين' }]

        edariaModel = { employeeCount: '', revisorsCount: '' };
        TaalemiaModel = {
            types: [{ id: 1, name: 'مدارس حكومية' }, { id: 2, name: 'مدارس عالمية واهلية' }, { id: 3, name: 'جامعات' }], personCount: '', selectedType: { id: 1, name: 'مدارس حكومية' }
        }
        masjedModel = {
            types: [{ id: 1, name: 'داخل حي' }, { id: 2, name: 'قرب سوق' }], personCount: '', selectedType: { id: 1, name: 'داخل حي' }
        };
        darModel = { personCount: '' };
        countLabel = '';
        PersonsCount!: number;
        result = 0;
        comulativeResult = 0;
        calculatedType = { name: '', result: 0 };
        calculatedTypes = [] as any[];
        isComulative = false;
        mounted() {
            //this.selectedBuildingType = this.buildingTypes[0];
            (this.$refs as any).buildingTypeSelectRef.selectedIndex = 0
        }
        getConsumption() {
            this.isComulative = true;
            //debugger;// eslint-disable-line no-debugger
            if (this.selectedBuildingType.id === 1) {//مباني سكنية
                if (this.selectedHousingType.id === 1) {
                    this.result = this.PersonsCount * 0.162;
                }
                else if (this.selectedHousingType.id === 2) {
                    this.result = this.PersonsCount * 0.15;
                }
                else if (this.selectedHousingType.id === 3) {
                    this.result = this.PersonsCount * 0.1;
                }
                else if (this.selectedHousingType.id === 4) {
                    this.result = this.PersonsCount * 0.12;
                }
                else if (this.selectedHousingType.id === 5) {
                    this.result = this.PersonsCount * 0.15;
                }
            }
            else if (this.selectedBuildingType.id === 2) {
                if (this.edariaModel.revisorsCount === '') {
                    this.result = (parseInt(this.edariaModel.employeeCount) * 0.025);
                }
                else {
                    this.result = (parseInt(this.edariaModel.employeeCount) * 0.025)
                        + (parseInt(this.edariaModel.revisorsCount) * 0.005);
                }
            }
            else if (this.selectedBuildingType.id === 3) {
                if (this.TaalemiaModel.selectedType.id === 1) {
                    this.result = parseInt(this.TaalemiaModel.personCount) * 0.015;
                }
                else if (this.TaalemiaModel.selectedType.id === 2) {
                    this.result = parseInt(this.TaalemiaModel.personCount) * 0.02;
                }
                else {
                    this.result = parseInt(this.TaalemiaModel.personCount) * 0.015;
                }
            }
            else if (this.selectedBuildingType.id === 4) {
                if (this.masjedModel.selectedType.id === 1) {
                    this.result = parseInt(this.masjedModel.personCount) * 0.005;
                }

                else {
                    this.result = parseInt(this.masjedModel.personCount) * 0.01;
                }
            }
            else if (this.selectedBuildingType.id === 5) {
                this.result = parseInt(this.darModel.personCount) * 0.5;
            }
            else if (this.selectedBuildingType.id === 6) {
                this.result = parseInt(this.darModel.personCount) * 0.45;
            }
            else {//7
                this.result = parseInt(this.darModel.personCount) * 0.18;
            }
            //if (!this.isComulative) {
            //    this.calculatedTypes = [];
            //    this.calculatedTypes.push({ name: this.selectedBuildingType.name, result: this.result });
            //}
            //else {
                var result = this.result;
                this.calculatedTypes.push({ name: this.selectedBuildingType.name, result: this.result });
                this.comulativeResult += this.result;
                
            //}
           /* debugger;// eslint-disable-line no-debugger*/
            (this.$refs as any).buildingTypeSelectRef.selectedIndex = "0"
            this.selectedBuildingType = ""
           

        }


        onChange() {
            if (this.selectedBuildingType.id === 5) {
                this.countLabel = 'اختر عدد الأَسِرَّة';
            }
            else if (this.selectedBuildingType.id === 6) {
                this.countLabel = 'أدخل عدد العجزة';
            }
            else {
                this.countLabel = 'أدخل عدد المحتجزين';
            }
        }
        addResult() {
            var isValid = (this.$refs as any).buildingTypeSelectRef.checkValidity();
            if (isValid) {


                if (this.isComulative == false) {
                    this.calculatedTypes = [];
                    this.comulativeResult = 0;
                    this.isComulative = true;
                }
            }
            //this.isComulative = true;

        }
        getResult() {
            this.isComulative = false;
        }
        formattedResult(result: number) {
            var stringarr = result.toString().split('.');
            //var formatted = `متر مكعب و${stringarr[1]} لتر${stringarr[0]}`
            var formatted = `${parseInt(stringarr[0])} متر مكعب و ${parseInt(stringarr[1])} لتر`;
            if (stringarr.length === 2 && parseInt(stringarr[0]) === 0) {
                formatted = ` ${parseInt(stringarr[1])} لتر`;
            }
            if (stringarr.length === 2 && parseInt(stringarr[1]) === 0) {
                formatted = `${parseInt(stringarr[0])} متر مكعب`;
            }

            return formatted;
        }
        reset() {
            location.reload();

            //(this.$refs as any).buildingTypeSelectRef.selectedIndex = 0
            //this.calculatedTypes = [];
            //this.comulativeResult = 0;
            //this.isComulative = true;
        }
    }


