
    import { Vue } from 'vue-class-component';
    import { TarsheedDataModel, BuildingTypesLookupDTO, BuildingsDTO, ConsumptionReadingsDTO, WaterMetersDTO, AnotherWaterSourcesDetailsDTO, AnotherWaterSourcesDTO } from "../models/tarsheedModel";
    import TarsheedService from "../services/tarsheedServices";
    import { ToasterService } from "../services/toasterService";
    import AuthenticationService from "../services/authenticationService";
    import { LoginModel } from '../models/loginModel';


    export default class LoginPage extends Vue {
        inputUsername = 'admin';
        inputPassword = 'admin123qwe';
        username = "admin";
        password = "admin123qwe";
        isInvalidLogin = false;
        loginModel = {} as LoginModel
        isLoader = false;
        login() {
            this.isLoader = true;
            AuthenticationService.login(this.loginModel).then(response => {
                this.isInvalidLogin = false;
                console.log("response", response);
                this.$cookies.set("userData", response);
                localStorage.setItem("ur", response.userRole);
                var cookieValue = AuthenticationService.getCookie("userData");
                /*this.$cookies.get*/
                console.log(cookieValue);
                this.$router.push({ name: 'recordForm' });

            }).catch(err => {
                this.isInvalidLogin = true;
            }).finally(() => {
                this.isLoader = false;
                console.log(this.isLoader);
            });
        }
    }
