import http from "@/http-common";

import { LoginModel } from "../models/loginModel";
import router from "@/router/index";
import { AddUserModel } from "../models/addUserModel";
import { CustomResponse } from "../models/response";
class AuthenticationService  {
    username = "admin";
    password = "admin123qwe";
    getCookie(key: string) {
        const data = (window as any).$cookies.get(key);
        return data;
    }
    isAuthenticated() {
        //debugger // eslint-disable-line no-debugger

        const data = this.getCookie("userData");
        console.log(data);
        let isAuth = false;
        if (data && data.token) {
            //const splittedData = data.token
            //if (splittedData[0] === this.username && splittedData[1] === this.password) {
            //    isAuth= true;
            //}
            //else {
            //    isAuth = false;
            //}
            isAuth = true;
        }
        return isAuth;
    }
    async login(model: LoginModel): Promise<any> {
        const response = await http.post("/api/authentication/login", model);
        return response.data
    }
    async register(model: AddUserModel): Promise<CustomResponse> {
        const response = await http.post("/api/authentication/register", model);
        return response.data
    }
    logout() {
        (window as any).$cookies.remove('userData');
        router.push({ name: "login" });
    }


}
export default new AuthenticationService();